
<template>
  <div class="hello">

    <el-container style="height: 100%; border: 0px solid #eee" direction="vertical">
        <el-header style="text-align: right; font-size: 12px; height: 140px">

          <el-avatar :size="large" :src="require('@/assets/patrick.png')"
              style="position: absolute; right:240px; top: 40px;
                     height:50px; width: 50px; fit: cover"></el-avatar>

          <img
            style="width: 114px; height: 28px; position:absolute; left:40px; top:50px"
            src="../../assets/yys_icon2_with_text.png"></img>

          <div class="user_name_text">
            zjwlk123
          </div>

          <div class="user_role_text">
            管理员
          </div>

          <el-button class="logout_button" type="info" style = "position:absolute; top:45px; right:40px;
                                              width:96px; height: 38px; border-radius: 10px;
                                              " icon="el-icon-switch-button" @click="logout">登出</el-button>

        </el-header>

          <el-button type="primary" style = "position: absolute; right: 40px; top: 130px;
                                             border-radius: 10px; height: 38px; width: 96px" @click="changeAdClicked" v-show="showAddChangeButton" >更换广告</el-button>

          <el-dialog title="更换广告" :visible.sync="showContentChangeDialog">
            <el-form :model="form">
              <el-form-item label="广告组" :label-width="formLabelWidth">
                <el-select v-model="form.ad_index" placeholder="请选择广告">
                  <el-option
                      v-for="item in ad_data_with_placeholder"
                      :key="item.name"
                      :label="item.name"
                      :value="item.index">
                  </el-option>
                  <!--                  <el-option label="蓝莓锅里酸奶块" value="blueberry"></el-option>-->
                  <!--                  <el-option label="炫迈口香糖" value="strike"></el-option>-->
                </el-select>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="cancelAdChange">取 消</el-button>
              <el-button type="primary" @click="confirmAdChange">确 定</el-button>
            </div>
          </el-dialog>

          <el-tabs v-model="activeName" @tab-click="handleTabChange"
                   style="padding-left: 40px; padding-right: 40px;">
            <el-tab-pane label="设备与场景" name="tab_devices" >
              <el-table ref="multipleTable" :data="tableData"
                        tooltip-effect="dark" @selection-change="handleSelectionChange"
                        v-loading="loading" v-if="showTable" :row-class-name="rowStyle"
                        :header-cell-style="{background:'#eef1f6',color:'#606266'}">

                <el-table-column fixed="left" type="selection" :selectable="checkSelectable"></el-table-column>
                <el-table-column prop="id" label="ID" show-overflow-tooltip></el-table-column>
                <el-table-column prop="connection_status" label="在线状态" show-overflow-tooltip></el-table-column>
                <el-table-column prop="device_type" label="设备类型" show-overflow-tooltip></el-table-column>
                <el-table-column prop="screen_size" label="屏幕尺寸" show-overflow-tooltip></el-table-column>
                <el-table-column prop="gateway_mac" label="所属网关"  show-overflow-tooltip></el-table-column>
                <el-table-column prop="ad_name" label="广告名称" show-overflow-tooltip></el-table-column>
                <el-table-column prop="action_count" label="扫码次数" show-overflow-tooltip></el-table-column>
                <el-table-column prop="battery_level" label="电量" show-overflow-tooltip></el-table-column>

              </el-table>
            </el-tab-pane>


            <el-tab-pane label="广告监测" name="tab_ads_supervising">

              <el-table ref="adTable" :data="ad_data_without_placeholder"
                        tooltip-effect="dark"
                        v-loading="ad_table_loading"
                        v-if="showTable"
                        :header-cell-style="{background:'#eef1f6',color:'#606266'}">

                <el-table-column prop="id" label="广告Id" show-overflow-tooltip></el-table-column>
                <el-table-column prop="name" label="广告名" show-overflow-tooltip></el-table-column>
                <el-table-column prop="scan_count" label="总扫码数" show-overflow-tooltip></el-table-column>
                <el-table-column prop="display_count" label="当前设备数" show-overflow-tooltip></el-table-column>
                <el-table-column prop="last_scan_time" label="最近扫码时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="last_24h_scan_time" label="24小时扫码数" show-overflow-tooltip></el-table-column>


              </el-table>

            </el-tab-pane>

          </el-tabs>

    </el-container>
  </div>
</template>

<script>
import router from "@/router";
const axios = require('axios').default;

export default {
  name: 'Home',
  props: {
    msg: String
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData()
  },
  data() {
    return {
      showTable: true,
      showContentChangeDialog: false,
      showAddChangeButton: true,
      ad_table_loading: false,
      activeName: 'tab_devices',
      headers :{
        'Authorization': 'Bearer ' + window.sessionStorage.getItem('myToken')
      },
      tableData: [],
      labelMacs: [],
      ad_data_with_placeholder:[
        { index: -1,
          name: '请选择广告'
        },
        { index: 0,
          name: '拼多多',
        }, {
          index: 1,
          name: 'KFC',
        },{
          index: 2,
          name: '中国移动浙江营业厅',
        }
      ],
      ad_data_without_placeholder: [
       { index: 0,
          id: '2U23Khvs',
          name: '拼多多',
          barcode_without_mac: 'AD001',
          small_demo_id: '4f007db3176e4af0bbb0b6c6ad7a5078',
          large_demo_id: '409f2c0bd00747d183730b971418452d',
          temp_display_count: 0,
          display_count: 0
        }, {
          index: 1,
          id: 'HmBJqIlr',
          name: 'KFC',
          barcode_without_mac: 'AD002',
          small_demo_id: 'f3b329fbefc4497b8c31ebf4f5dba9b9',
          large_demo_id: '3fe7663de422434a803f74648bb33928',
          temp_display_count: 0,
          display_count: 0
        },{
          index: 2,
          id: 'F9mOXr3Z',
          name: '中国移动浙江营业厅',
          barcode_without_mac: 'AD003',
          small_demo_id: '2278c4dd74f0496a8df356ad75e10a86',
          large_demo_id: 'f8124e33ad77432fbedcc4042339a9cc',
          temp_display_count: 0,
          display_count: 0
        }
      ],
      loading:true,
      multipleSelection: [],
      param_for_querying_labels : {page : 1, size: 1000, storeUuid : '0108'},
      param_for_querying_gateways : {page : 1, size: 1000, storeUuid : '0108'},
      form: {
        ad_index : -1
      },
      formLabelWidth: '120px'
    }
  },

  methods: {

    logout(){
      console.log('logout pressed')
      window.sessionStorage.removeItem('myToken');
      router.push('/login')
    },

    fetchData(){
      this.loading = true
      let self = this

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      axios.all([

        axios.post('V2/pub/label/query',this.param_for_querying_labels, {
          headers: this.headers,
          params: this.param_for_querying_labels
        }),

        axios.get('V2/gateway', {
          headers: this.headers,
          params: this.param_for_querying_gateways
        })

      ]).then(checkFailed(([response1, response2]) => {
        self.fetchLabelDataSucceeded(response1)
        self.fetchGatewayDataSucceeded(response2)
      })).catch(axios.spread((error1, error2) => {
            self.fetchLabelDataFailed(error1)
            self.fetchGatewayDataFailed(error2)
          }
      ))


    },

    // Handling label data
    fetchLabelDataSucceeded(response){
      console.log('label fetching succeeded')
      console.log(response.data)

      let labelInfoList = response.data.body.list
      console.log(labelInfoList.length + ' labels found')
      let self = this

      for (let i = 0; i < labelInfoList.length; i++) {

        let labelInfo = labelInfoList[i]
        let mac = labelInfo.mac
        this.labelMacs.push(mac)
        let batteryLevel = labelInfo.battery
        let screenSizeText = labelInfo.screenSize
        let connection_status = '离线'
        let gateway_mac = ''
        if(labelInfo.correspond != null){
            gateway_mac = labelInfo.correspond.gatewayMac
        }

        if(labelInfo.heartbeat != '0'){
          connection_status = '在线'
        }

        this.tableData.push({
          id : mac,
          battery_level: batteryLevel,
          screen_size: this.decodeScreenSize(screenSizeText),
          device_type:'屏幕',
          connection_status: connection_status,
          gateway_mac: gateway_mac,
          ad_name : ''
        })
      }

      self.fetchGoodsBinding()
      // self.fetchLabelTemplateBinding()

      this.loading = false
    },

    fetchLabelDataFailed(error){
      if(error == null) return
      this.loading = false
      this.$message.error('设备信息拉取失败');
      console.log('label fetching failed')
    },

    decodeScreenSize(screenSizeText){
      if(screenSizeText === '000c'){
        return 4.2
      }else if(screenSizeText === '000f'){
        return 5.83
      }
      return 'not defined'
    },

    //Handling gateway data
    fetchGatewayDataSucceeded(response){
      console.log('gateway fetching succeeded')
      console.log(response.data)
      let gatewayInfoList = response.data.list
      console.log(gatewayInfoList.length + ' gateways found')

      for (let i = 0; i < gatewayInfoList.length; i++) {
        let gatewayInfo = gatewayInfoList[i]
        let mac = gatewayInfo.mac
        this.tableData.push({
          id : mac,
          connection_status: gatewayInfo.mode,
          device_type:'网关',
          screen_size: 'N/A',
          battery_level: 'N/A'
        })
      }

      this.loading = false
    },

    fetchGatewayDataFailed(error){
      if(error == null) return
      this.loading = false
      this.$message.error('网关信息拉取失败');
      console.log('gateway fetching failed')
    },

    fetchGoodsBinding(){
      let self = this
      console.log('fetch goods binding called')
      let goods_requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < this.labelMacs.length; i++) {
        goods_requests.push(axios.get('/V2/label/query', {
          headers: this.headers,
          params: {mac: this.labelMacs[i]}
        }))
      }

      axios.all(goods_requests).then(checkFailed((responses) => {
        console.log(responses)
        for (let i = 0; i < self.ad_data_without_placeholder.length; i++) {
            const ad_data = self.ad_data_without_placeholder[i]
            ad_data.temp_display_count = 0;
        }
        for (let i = 0; i < responses.length ; i++) {
          let response = responses[i]
          if(response.data.status!=200)continue
          console.log(response)

          let good_barcode = response.data.body.id
          let good_name = response.data.body.label4

          let thisMac = response.request.responseURL.split('=')[1]

          console.log('good result successfully fetched for ' + thisMac)

          for (let j = 0; j < self.tableData.length; j++) {
            const labelData = self.tableData[j]
            if (labelData.id == thisMac) {
              console.log(good_barcode.substring(0, good_barcode.length-12))
              labelData.good_barcode_without_mac = good_barcode.substring(0, good_barcode.length-12)
              labelData.good_barcode = good_barcode
              labelData.good_name = good_name

              let found_matching_ad = false

              for (let k = 0; k < self.ad_data_without_placeholder.length; k++) {
                const adData = self.ad_data_without_placeholder[k]
                if(adData.barcode_without_mac == labelData.good_barcode_without_mac){
                  labelData.ad_name = adData.name
                  adData.temp_display_count++
                  found_matching_ad = true
                  break
                }
              }

              if(!found_matching_ad){
                labelData.ad_name = good_name
              }

              break
            }
          }

        }

        for (let i = 0; i < self.ad_data_without_placeholder.length; i++) {
          const ad_data = self.ad_data_without_placeholder[i]
          ad_data.display_count = ad_data.temp_display_count
          ad_data.temp_display_count = 0
        }

        self.reloadTable()
        self.fetchActionCount()
      })).catch(axios.spread((errors) => {
            console.log(errors)
          }
      ))
    },

    fetchActionCount(){
      let self = this
      console.log('fetch action count called')
      let requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < self.tableData.length; i++) {
          let labelData = self.tableData[i]
          let mac = labelData.id
          let ad_name = labelData.ad_name
          let ad_id = ''

          for (let j = 0; j < self.ad_data_without_placeholder.length; j++) {
              let adData = self.ad_data_without_placeholder[j]
              if(ad_name == adData.name){
                ad_id = adData.id
                break
              }
          }

          if(ad_id == '') continue

          requests.push(axios.get('/lp/get-action-count', {
            params: {
              adId: ad_id,
              mac: mac
            },
            baseURL: 'http://81.68.165.124:8080/'
            // baseURL: 'http://192.168.1.127:8080/'
          }))

          axios.all(requests).then(checkFailed((responses) => {
            console.log(responses)
            for (let i = 0; i < responses.length ; i++) {
              let response = responses[i]

              console.log(response)

              let thisMac = response.request.responseURL.split('mac=')[1]
              thisMac = thisMac.split("&")[0]

              console.log(thisMac)

              for (let j = 0; j < self.tableData.length; j++) {
                  const labelData = self.tableData[j]
                  if(labelData.id == thisMac){
                      labelData.action_count = response.data
                  }
              }

            }

            self.reloadTable()

          })).catch(axios.spread((errors) => {
                console.log(errors)
              }
          ))

      }


    },

    fetchLabelTemplateBinding(){
      let self = this
      let templates_requests = []

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      for (let i = 0; i < this.labelMacs.length; i++) {
        let thisMac = this.labelMacs[i]
        templates_requests.push(axios.post('/V2/pub/binding/query', {mac: thisMac}, {
          headers: this.headers,
          params: {mac: thisMac},
        }))
      }

      axios.all(templates_requests).then(checkFailed((responses) => {
        console.log(responses)
        for (let i = 0; i < responses.length ; i++) {
          let response = responses[i]
          console.log(response)
          if(response.data.status == 431) continue
          let template_id = response.data.body.templets[0].demoId
          let template_name = response.data.body.templets[0].demoName
          let thisMac = response.request.responseURL.split('=')[1]
          console.log(thisMac)

          for (let j = 0; j < self.tableData.length; j++) {
            const labelData = self.tableData[j]
            if (labelData.id == thisMac){
              labelData.demo_id = template_id
              labelData.demo_name = template_name
              break
            }
          }
          self.reloadTable()
        }

      })).catch(axios.spread((errors) => {
            console.log(errors)
          }
      ))
    },

    fetchAdDetails(){
      this.ad_table_loading = true
      let adDetailRequests = []

      for (let i = 0; i < this.ad_data_without_placeholder.length; i++) {
        adDetailRequests.push(axios.get('/lp/get-ad-detail', {
          params: {
            adId: this.ad_data_without_placeholder[i].id
          },
          baseURL: 'http://81.68.165.124:8080/'
          // baseURL: 'http://192.168.1.127:8080/'
        }))
      }

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      let self = this

      axios.all(adDetailRequests).then(checkFailed((responses) => {
        console.log(responses)

        for (let i = 0; i < responses.length; i++) {
          const response = responses[i]
          const ad_id = response.data.adId
          for (let j = 0; j < self.ad_data_without_placeholder.length; j++) {
              const ad_data = self.ad_data_without_placeholder[j]
              if(ad_data.id == ad_id){
                ad_data.scan_count = response.data.scanCount
                ad_data.last_24h_scan_time = response.data.scanCount24H
                ad_data.last_scan_time = response.data.lastScanTime
                break
              }
          }

        }

        self.ad_table_loading = false
        this.reloadTable()
      })).catch(axios.spread((errors) => {
            console.log(errors)
          }
      ))

    },

    changeAdClicked(){
      console.log('changeAdClicked')
      console.log('current selection: ' , this.multipleSelection)

      if(this.multipleSelection.length == 0){
        this.$message.error('请至少选择一个屏幕');
      }else{
        this.showContentChangeDialog = true
      }


    },

    cancelAdChange(){
      console.log('cancelAdChange')
      this.showContentChangeDialog = false
      this.form.ad_index = -1
    },

    confirmAdChange(){
      this.showContentChangeDialog = false

      if(this.form.ad_index == -1){
        return
      }

      let ad_info = this.ad_data_without_placeholder[this.form.ad_index]
      let update_requests = []

      for (let i = 0; i < this.multipleSelection.length; i++) {
        let mac = this.multipleSelection[i].id
        let demo_id = ad_info.small_demo_id
        if (this.multipleSelection[i].screen_size == 5.83) {
          demo_id = ad_info.large_demo_id
        }
        let update_params = {
          mac: mac,
          barcode: ad_info.barcode_without_mac + mac,
          demoId: demo_id,
          storeId: '0108'
        }

        update_requests.push(axios.post('/V2/pub/bind/update', update_params, {
          headers: this.headers,
        }))
      }

      function checkFailed (then) {
        return function (responses) {
          const someFailed = responses.some(response => response.error)

          if (someFailed) {
            throw responses
          }

          return then(responses)
        }
      }

      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });

      let self = this
      axios.all(update_requests).then(checkFailed((responses) => {
          console.log(responses)
          // for (let i = 0; i < responses.length; i++) {
          //   let response = responses[i]
          //   if(response.request.data.status != 200)continue
          //   let thisMac = JSON.parse(response.config.data).mac
          //   for (let j = 0; j < self.tableData.length; j++) {
          //       let data = self.tableData[j]
          //       if(data.id == thisMac){
          //         let demo_id = ad_info.small_demo_id
          //         if (this.multipleSelection[i].screen_size == 5.83) {
          //           demo_id = ad_info.large_demo_id
          //         }
          //         data.demo_id = demo_id
          //         data.good_id = ad_info.barcode
          //       }
          //   }
          //
          // }
          loading.close()
          // self.fetchLabelTemplateBinding()
          self.fetchGoodsBinding()
          // this.fetchData()
      })).catch(axios.spread((errors) => {
            console.log(errors)
            loading.close()
          }
      ))

      this.form.ad_index = -1

    },

    reloadTable(){
      this.showTable = false
      this.$nextTick(() =>{
        this.showTable = true
      })
    },

    checkSelectable(row){
      return row.device_type != '网关' && row.connection_status == '在线'
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    handleTabChange(tab, event){
      console.log(tab)
      this.showAddChangeButton = this.activeName == 'tab_devices';
      if(this.activeName == 'tab_devices'){
        this.fetchGoodsBinding()
      }else if(this.activeName == 'tab_ads_supervising'){
        this.fetchAdDetails()
      }
    },

    rowStyle({row, rowIndex}) {
      // if (row.connection_status != '在线') {
      //   console.log(rowIndex)
      //   return {"background-color" : "#e9c8c3"}
      // }
      for (let i = 0; i < this.multipleSelection.length; i++) {
        if(row.id === this.multipleSelection[i].id){
          return 'selectedStyle'
        }
      }

    }
  }
}


</script>

